import React, {useState} from "react";

const QuestionBox = ({question, options, selected}) => {
    const [answer, setAnswer] = useState(options);
    const antwoord = options[Math.floor(Math.random() * options.length)];
    const beginPart = "//commons.wikimedia.org/wiki/File:nl-" + antwoord + ".ogg?embedplayer=yes";
    return (
        <div className="questionBox">
            <div className="question">Is it {question[0]} or {question[1]}?</div>
            <iframe
              src= {beginPart}

              width="300"
              height="20"
              frameBorder="0">
            </iframe>
            {answer.map((text, index) => (
                <button
                    key={index}
                    className="answerBtn"
                    onClick={() => {
                      if (text === antwoord) {
                        selected(1);
                      }
                      else {
                        selected(0);
                      }
                    }}
                >
                    {text}
                </button>
            ))}
        </div>
    );
};

export default QuestionBox;